export namespace MaterialidadEnums {
  export const factores = [
    'Ingresos brutos',
    'Resultado del Ejercicio',
    'Patrimonio',
    'Activos Netos',
    'Gastos',
    'Otro'
  ]

  export const niveles = [ 'Alto', 'Moderado', 'Bajo' ]
}

export namespace MaterialidadUtils {
  declare var app: any
  export function getNivel(nivel: number): 'min' | 'med' | 'max' {
    switch ('' + nivel) {
      case '1':
        return 'max'
      case '2':
        return 'med'
      case '3':
        return 'min'
    }
  }

  export function getDefaulPorcMG(factor: number, nivel: number) {
    if (!factor || !nivel) {
      return null
    }

    const field = `monto${factor}_${nivel}`

    switch (field) {
      case 'monto1_3':
      case 'monto3_3':
      case 'monto4_3':
      case 'monto5_3':
      case 'monto6_3':
        return 0.5
      case 'monto2_3':
        return 3
      case 'monto1_2':
        return 0.75
      case 'monto2_2':
        return 5
      case 'monto3_2':
      case 'monto4_2':
      case 'monto5_2':
      case 'monto6_2':
      case 'monto1_1':
        return 1
      case 'monto2_1':
        return 7
      case 'monto3_1':
      case 'monto4_1':
      case 'monto5_1':
      case 'monto6_1':
        return 2
    }
  }

  export function calculateTotal(
    field: 'porc_general' | 'porc' | 'porc_error',
    materialidad: Partial<ProyectoMaterialidad>,
    factor: number | false,
    nivel: number | false
  ): number {
    if (factor === false) {
      factor = materialidad.factor
    }

    if (nivel === false) {
      nivel = materialidad.nivel
    }

    if (!factor || !nivel || typeof materialidad.montos[factor] === 'undefined') {
      return 0
    }

    let nivel_field = MaterialidadUtils.getNivel(nivel as 1 | 2 | 3)

    if (field == 'porc_general') {
      let monto = materialidad.montos[factor].valor
      let porc = materialidad.montos[factor][nivel_field]
      if (porc == null) {
        porc = MaterialidadUtils.getDefaulPorcMG(factor, nivel)
      }
      return +Math.abs(Math.round(monto * porc)) / 100
    } else {
      let monto = calculateTotal('porc_general', materialidad, factor, nivel)
      let porc = materialidad[field]

      return +Math.abs(Math.round(monto * porc)) / 100
    }
  }

  type MaterialidadChangeServerParam = {
    field: keyof ProyectoMaterialidad | string
    value: any
  }

  export function sendToServer(
    id_visita: string,
    change: MaterialidadChangeServerParam,
    materialidad: Partial<ProyectoMaterialidad>,
    params: any,
    cb?: (response: any) => void
  ) {
    materialidad = Object.assign({}, materialidad, { [change.field]: change.value })
    const url = url_for(`proyecto/${app.vars.idproyecto}/materialidad/${id_visita}/update`)
    let changes: MaterialidadChangeServerParam[] = [ change ]
    if (change.field != 'factor_justificacion') {
      changes.push({ field: 'total', value: calculateTotal('porc_general', materialidad, false, false) })
      changes.push({ field: 'total_planeacion', value: calculateTotal('porc', materialidad, false, false) })
      changes.push({ field: 'total_error', value: calculateTotal('porc_error', materialidad, false, false) })
    }
    $.post(url, _.merge(params, { changes })).done((res) => {
      if ($.isFunction(cb)) {
        cb(res)
      }
    })
  }
}
