import { Component, SyntheticEvent } from 'react'
import cx from 'classnames'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'

import { MaterialidadEnums, MaterialidadUtils } from './MaterialidadUtils'
import HojaTrabajoContext from '../proyecto__hoja_trabajo/HojaTrabajoContext'
import { HojaTrabajoUpdateUtils } from '../proyecto__hoja_trabajo/utils'
import InputAutoNumeric from '../global/InputAutoNumeric'

type MaterialidadProps = {
  materialidad: ProyectoMaterialidad
  isEditable: boolean
}

type MaterialidadPercentTableState = {
  montos: ProyectoMaterialidadMonto
}

export default class MaterialidadPercentTable extends Component<MaterialidadProps, MaterialidadPercentTableState> {
  static contextType = HojaTrabajoContext
  context: HojaTrabajoContextType

  private static changed = new Set()
  constructor(props: MaterialidadProps) {
    super(props)

    this.state = {
      montos: props.materialidad ? props.materialidad.montos : {}
    }
  }

  onChangeMonto(ev: SyntheticEvent, value: number, factor: number, nivel: number, persist: boolean) {
    if (persist && (ev.target as any).value == '') {
      value = MaterialidadUtils.getDefaulPorcMG(factor, nivel)
    }

    let field: string
    this.setState(
      (state) => {
        let { montos } = state
        if (typeof montos[factor] == 'undefined') {
          montos[factor] = { valor: 0 }
        }

        const nivel_field = MaterialidadUtils.getNivel(nivel)

        field = `monto${factor}_${nivel_field}`

        if (montos[factor][nivel_field] != value) {
          montos[factor][nivel_field] = value
          MaterialidadPercentTable.changed.add(field)
        }

        montos[factor][nivel_field] = value

        return { montos }
      },
      () => {
        if (persist && MaterialidadPercentTable.changed.has(field)) {
          this.sendToServer({ field, value })
          MaterialidadPercentTable.changed.delete(field)
        }
      }
    )
  }

  sendToServer(change: any) {
    const materialidad = Object.assign({}, this.props.materialidad, { montos: this.state.montos })
    MaterialidadUtils.sendToServer(this.props.materialidad.Visita.id_visita, change, materialidad, {}, (response) => {
      if (response && response.response.visita) {
        const { hoja_trabajo, update } = this.context
        const visita = this.props.materialidad.Visita
        let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, visita, response.response.visita)
        update(hoja_trabajo_new)
      }
    })

    const { hoja_trabajo, update } = this.context
    let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, this.props.materialidad.Visita, {
      materialidad
    })
    update(hoja_trabajo_new)
  }

  getMonto(factor: number, nivel: number) {
    let monto = this.state.montos[factor][MaterialidadUtils.getNivel(nivel)]
    if (monto == null) {
      monto = MaterialidadUtils.getDefaulPorcMG(factor, nivel)
    }
    return monto
  }

  render() {
    return (
      <Modal
        closeIcon
        trigger={<Button color='orange' floated='left' compact icon='table' content='Tabla de porcentajes' />}
        className='modal-ab'
      >
        <Modal.Header content='Porcentajes y Montos de Materialidades' />
        <Modal.Content>
          <table className='table table-striped table-condensed' id='materialidad-percent-table'>
            <thead>
              <tr className='th1'>
                <th>&nbsp;</th>
                <th colSpan={3}>Porcentajes de Materialidades</th>
                <th colSpan={3}>Montos de Materialidades</th>
              </tr>
              <tr className='th2'>
                <th>Factor</th>
                <th>Mx %</th>
                <th>Moderado %</th>
                <th>Mn %</th>
                <th>Mx</th>
                <th>Moderado</th>
                <th>Mn</th>
              </tr>
            </thead>
            <tbody>
              {MaterialidadEnums.factores.map((factor, i) => {
                return (
                  <tr key={`tr-factor-${i}`} className={i + 1 == this.props.materialidad.factor ? 'selected' : ''}>
                    <th className='td-factor'>{factor}</th>
                    {MaterialidadEnums.niveles.map((nivel, j) => {
                      return (
                        <td
                          key={`td-${i}-${j}-1`}
                          className={cx([ 'td-nivel', j + 1 == this.props.materialidad.nivel ? 'selected' : '' ])}
                        >
                          <InputAutoNumeric
                            value={this.getMonto(i + 1, j + 1)}
                            onChange={(ev: SyntheticEvent, value: number) =>
                              this.onChangeMonto(ev, value, i + 1, j + 1, false)}
                            onBlur={(ev: SyntheticEvent, value: number) =>
                              this.onChangeMonto(ev, value, i + 1, j + 1, true)}
                            minimumValue={'0'}
                            maximumValue={'100'}
                            className='percent input-mini'
                            isEditable={this.props.isEditable}
                          />
                        </td>
                      )
                    })}
                    {MaterialidadEnums.niveles.map((nivel, j) => {
                      return (
                        <td
                          key={`td-${i}-${j}-2`}
                          className={cx([
                            'td-total col-numeric',
                            j + 1 == this.props.materialidad.nivel ? 'selected' : ''
                          ])}
                        >
                          {accounting.formatMoney(
                            MaterialidadUtils.calculateTotal('porc_general', this.state, i + 1, j + 1)
                          )}
                        </td>
                      )
                    })}
                  </tr>
                )
              })}
            </tbody>
          </table>
        </Modal.Content>
      </Modal>
    )
  }
}
