import { Component } from 'react'
import EditorPreview from '../global/EditorPreview'
import { AttachmentListPanel } from '../proyecto__hoja_trabajo/Attachments'
import Dimmer from 'semantic-ui-react/dist/commonjs/modules/Dimmer/Dimmer'
import Loader from 'semantic-ui-react/dist/commonjs/elements/Loader/Loader'

declare var app: any

type MaterialidadObservacionesEditorProps = {
  data: string
  attachments: ProyectoAttachment[]
  onChangeData: (data: string, cb: () => void) => void
  onChangeAttachments: (attachments: ProyectoAttachment[]) => void
  isEditable: boolean
}

type MaterialidadObservacionesEditorState = {
  isLoadingAttachments: boolean
}

export default class MaterialidadObservacionesEditor extends Component<
  MaterialidadObservacionesEditorProps,
  MaterialidadObservacionesEditorState
> {
  state = {
    isLoadingAttachments: false
  }

  fetchAttachments(showLoading: boolean = true) {
    if (showLoading) {
      this.setState({ isLoadingAttachments: true })
    }

    const url = url_for(`proyecto/${app.vars.idproyecto}/hoja-trabajo/attachments`)
    $.ajax(url, {
      type: 'GET',
      data: { model: 'materialidad__obs', model_id: app.vars.current_idvisita },
      dataType: 'json'
    }).done((res) => {
      this.props.onChangeAttachments(res.response.Attachments)
      this.setState({ isLoadingAttachments: false })
    })
  }

  onUpdateAttachment = () => {
    this.fetchAttachments()
  }

  onDeleteAttachment = (adjunto: ProyectoAttachment, cb: () => void) => {
    this.setState({ isLoadingAttachments: true })

    const h = public_path(`uploads/proyecto_adjunto/${adjunto.filepath}`, true).replace(/\./g, '.').replace(/\\/g, '/')
    const t = '<a .*href="' + h + '"(.+?)/a>s*(<br/?>)?'
    const re = new RegExp(t, 'g')
    const data_new = this.props.data.replace(re, '')

    if ($.isFunction(cb)) {
      cb()
    }
    this.props.onChangeData(data_new, () => this.fetchAttachments())
  }

  onChangeData = (editor: CKEDITOR.editor, closeModal: boolean, autosave: boolean, cb: () => void) => {
    this.props.onChangeData(editor.getData(), () => {
      if ($.isFunction(cb)) {
        cb()
      }
      this.fetchAttachments()
    })
  }

  render() {
    const ckeditorOptionsObs = {
      uploadUrl: url_for(
        'proyecto/' +
          app.vars.idproyecto +
          '/master_data/paso/' +
          app.vars.idpaso +
          '/upload?id_visita=' +
          app.vars.current_idvisita +
          '&model=materialidad__obs' +
          '&model_id=' +
          app.vars.current_idvisita
      ),
      imageUploadUrl: url_for(
        'proyecto/' +
          app.vars.idproyecto +
          '/master_data/paso/' +
          app.vars.idpaso +
          '/upload?id_visita=' +
          app.vars.current_idvisita +
          '&model=materialidad__obs' +
          '&model_id=' +
          app.vars.current_idvisita
      ),
      filebrowserUploadUrl: url_for(
        'proyecto/' +
          app.vars.idproyecto +
          '/master_data/paso/' +
          app.vars.idpaso +
          '/upload?id_visita=' +
          app.vars.current_idvisita +
          '&n=attachment&model=materialidad__obs' +
          '&model_id=' +
          app.vars.current_idvisita
      )
    }

    const attachmentsList = (
      <div style={{ position: 'relative' }}>
        <Dimmer inverted active={this.state.isLoadingAttachments}>
          <Loader inverted />
        </Dimmer>
        <AttachmentListPanel
          attachments={this.props.attachments}
          onUpdateItem={this.onUpdateAttachment}
          onDeleteItem={this.onDeleteAttachment}
        />
      </div>
    )

    return (
      <EditorPreview
        className='observaciones-text-editor'
        ckeditorOptions={ckeditorOptionsObs}
        data={this.props.data}
        required
        label=' '
        labelTitle='Documente la Razn de la Seleccin del Factor y Nivel de Confianza'
        autosave
        onSave={this.onChangeData}
        editable={this.props.isEditable}
        footer={attachmentsList}
        openOnClickPreview
      />
    )
  }
}
