import { Component } from 'react'

import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab'
import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu'

import HojaTrabajoContext from '../proyecto__hoja_trabajo/HojaTrabajoContext'
import { AppVisitaDefaultTab } from '../proyecto__hoja_trabajo/AppVisitaTab'
import TabPaneVisita from './TabPaneVisita'

declare var app: any
declare type HojaTrabajoAppMaterialidadState = {
  activeIndex: number
  hoja_trabajo: HojaTrabajo
}

declare type HojaTrabajoAppMaterialidadPane = {
  type: string
  menuItem: () => JSX.Element
  pane?: () => JSX.Element
}

export default class App extends Component<HojaTrabajoAppProps, HojaTrabajoAppMaterialidadState> {
  private panes: HojaTrabajoAppMaterialidadPane[] = []

  constructor(props: HojaTrabajoAppProps) {
    super(props)

    const visita_current = _.find(this.props.hoja_trabajo.visitas, { is_current: true })
    const activeIndex = visita_current ? visita_current.index : null

    if (visita_current) {
      app.vars.current_idvisita = visita_current.id_visita
    }

    this.state = {
      activeIndex,
      hoja_trabajo: this.props.hoja_trabajo
    }
  }

  componentDidMount() {
    /**
     * Ancho de la tab.
     */
    const totalWidth =
      this.state.hoja_trabajo.proyecto.isOpen && user_has_role_permission('hoja_trabajo_edit') ? 90 : 98
    const tabWidth: number = totalWidth / _.size(_.filter(this.panes, { type: 'sheet' }))

    $('#visita-tabs').find('.menu>a.visita-tab').css({ width: tabWidth + '%' })

    const activeIndex = location.hash.replace(/^#!visita-/, '')
    if (activeIndex) {
      this.setState({ activeIndex: +activeIndex })
      const visita = _.find(this.state.hoja_trabajo.visitas, { activeIndex })
      if (visita) {
        app.vars.current_idvisita = visita.id_visita
      }
    }
  }

  handleClick = (e: React.SyntheticEvent, { index }: { index: number }) => {
    this.setState({ activeIndex: index })
    const visita = _.find(this.state.hoja_trabajo.visitas, { index })
    app.vars.current_idvisita = visita.id_visita
    window.location.hash = '#!visita-' + index
  }

  update = (data: any) => {
    this.setState((state: HojaTrabajoAppState) => {
      let hoja_trabajo = Object.assign({}, state.hoja_trabajo, data)

      return { hoja_trabajo }
    })
  }

  render() {
    this.panes = []

    const { activeIndex } = this.state

    let visitas: ProyectoPasoVisita[] = _.values(this.state.hoja_trabajo.visitas)
    visitas = _.sortBy(visitas, 'position')

    _.forEach(visitas, (visita) => {
      if (!visita.id) {
        return
      }

      visita.HojaTrabajo = this.state.hoja_trabajo
      this.panes.push({
        type: 'sheet',
        menuItem: () => {
          return (
            <Menu.Item
              key={visita.id}
              index={visita.index}
              className='tab-nav-item visita-tab'
              name={visita.label}
              onClick={this.handleClick}
              active={activeIndex == visita.index}
            >
              <AppVisitaDefaultTab visita={visita} />
            </Menu.Item>
          )
        },
        pane: () => {
          return (
            <TabPaneVisita
              key={visita.id}
              active={activeIndex == visita.index}
              visita={visita}
              isEditable={this.state.hoja_trabajo.proyecto.isOpen && user_has_role_permission('hoja_trabajo_edit')}
            />
          )
        }
      })
    })

    if (this.panes.length == 0) {
      return (
        <div className='alert alert-block nocontent'>
          <h3>Sin visitas</h3>
          <p>
            <span className='icon-warning-sign' />Este proyecto no tiene visitas creadas.
          </p>
        </div>
      )
    }

    return (
      <HojaTrabajoContext.Provider value={{ hoja_trabajo: this.state.hoja_trabajo, update: this.update }}>
        <div id='visita-tabs' className={`paso-code-${this.state.hoja_trabajo.paso.code}  paso-default`}>
          <Tab panes={this.panes} activeIndex={activeIndex} renderActiveOnly={false} />
        </div>
      </HojaTrabajoContext.Provider>
    )
  }
}
