import { Component, SyntheticEvent } from 'react'

import Form from 'semantic-ui-react/dist/commonjs/collections/Form'
import Select from 'semantic-ui-react/dist/commonjs/addons/Select'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'

import cx from 'classnames'

import { GenericTooltip } from '../global/Tooltip'
import InputAutoNumeric from '../global/InputAutoNumeric'

import { MaterialidadEnums, MaterialidadUtils } from './MaterialidadUtils'
import HojaTrabajoContext from '../proyecto__hoja_trabajo/HojaTrabajoContext'
import { HojaTrabajoUpdateUtils, HojaTrabajoUtils } from '../proyecto__hoja_trabajo/utils'
import MaterialidadObservacionesEditor from './MaterialidadObservacionesEditor'

type MaterialidadFieldTotalProps = {
  field: string
  name: string
  tooltip: string
  width?: string
  calculateTotal: (field: string) => number
}

accounting.settings.currency.decimal = ','
accounting.settings.currency.thousand = '.'
accounting.settings.currency.format = '%s %v'
accounting.settings.currency.precision = 0

const MaterialidadFieldTotal = (props: MaterialidadFieldTotalProps) => {
  return (
    <div className={`box-total ${props.width ? props.width : 'ten'} wide column`}>
      <div className='box-content'>
        <GenericTooltip title={`Ayuda | ${props.name}`} iconColor='olive' text={props.tooltip} />
        <div className='box-inner'>
          <div className='title'>{props.name}</div>
          <div className='value'>{accounting.formatMoney(props.calculateTotal(props.field), '$')}</div>
        </div>
      </div>
    </div>
  )
}

type MaterialidadFormProps = {
  materialidad: ProyectoMaterialidad
  isEditable: boolean
}

export default class MaterialidadForm extends Component<MaterialidadFormProps, Partial<ProyectoMaterialidad>> {
  static contextType = HojaTrabajoContext
  context: HojaTrabajoContextType

  private static changed = new Set()

  constructor(props: MaterialidadFormProps) {
    super(props)

    this.state = {
      factor: props.materialidad ? props.materialidad.factor : null,
      nivel: props.materialidad ? props.materialidad.nivel : null,
      montos: props.materialidad ? props.materialidad.montos : {},
      porc: props.materialidad ? props.materialidad.porc : null,
      porc_error: props.materialidad ? props.materialidad.porc_error : null,
      factor_justificacion: props.materialidad ? props.materialidad.factor_justificacion : '',
      Attachments: props.materialidad ? props.materialidad.Attachments : []
    }
  }

  static getDerivedStateFromProps(nextProps: MaterialidadFormProps, prevState: Partial<ProyectoMaterialidad>) {
    if (nextProps.materialidad.factor != prevState.factor) {
      return { factor: nextProps.materialidad.factor }
    }

    if (nextProps.materialidad.nivel != prevState.nivel) {
      return { nivel: nextProps.materialidad.nivel }
    }

    return null
  }

  /**
   * @returns Retorna los factores como un listado de opciones para el componente Select.
   */
  getFactoresAsSelectChoices() {
    const choices = MaterialidadEnums.factores.map(function(item, k) {
      return { key: k + 1, value: k + 1 + '', text: item }
    })

    choices.unshift({ key: -1, value: '', text: '- Seleccionar -' })

    return choices
  }

  /**
   * @returns Retorna los niveles de confianza como un listado de opciones para el componente Select.
   */
  getNivelesAsSelectChoices() {
    const choices = MaterialidadEnums.niveles.map(function(item, k) {
      return { key: k + 1, value: k + 1 + '', text: item }
    })

    choices.unshift({ key: -1, value: '', text: '- Seleccionar -' })

    return choices
  }

  getPorcMG() {
    let { factor, montos } = this.state
    let nivel = MaterialidadUtils.getNivel(this.state.nivel)
    let porc = null

    if (
      typeof montos != 'undefined' &&
      typeof montos[factor] != 'undefined' &&
      typeof montos[factor][nivel] != 'undefined'
    ) {
      porc = montos[factor][nivel]
    }

    if (porc == null) {
      porc = MaterialidadUtils.getDefaulPorcMG(this.state.factor, this.state.nivel)
    }

    return porc
  }

  isFactorInvalid() {
    return !this.state.factor || typeof this.state.montos[this.state.factor] == 'undefined'
  }

  sendToServer(change: any, cb?: () => void) {
    if (!this.props.isEditable) {
      return
    }

    MaterialidadUtils.sendToServer(this.props.materialidad.Visita.id_visita, change, this.state, {}, (response) => {
      if (response && response.response.visita) {
        const { hoja_trabajo, update } = this.context
        const visita = this.props.materialidad.Visita
        let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, visita, response.response.visita)
        update(hoja_trabajo_new)
      }

      if ($.isFunction(cb)) {
        cb()
      }
    })

    this.updateContext()
  }

  updateContext() {
    const { hoja_trabajo, update } = this.context
    let materialidad = Object.assign({}, this.props.materialidad, this.state)
    let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, this.props.materialidad.Visita, {
      materialidad
    })
    update(hoja_trabajo_new)
  }

  onSelectChoice(field: 'factor' | 'nivel', value: any) {
    let change: any = {}
    change[field] = value

    this.setState(change, () => {
      this.sendToServer({ field, value })

      $.blockUI(create_blockUI_loading_message('Aplicando cambios'))
      const { hoja_trabajo, update } = this.context
      const materialidad = Object.assign({}, this.props.materialidad, change)
      let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, this.props.materialidad.Visita, {
        materialidad
      })
      update(hoja_trabajo_new)

      const visitas = HojaTrabajoUtils.getAllVisitasAfter(hoja_trabajo, this.props.materialidad.Visita)
      let promises: Promise<string>[] = []
      _.forEach(visitas, (visita) => {
        if (!visita.id) {
          return
        }

        if (visita.id_status != '1') {
          return
        }

        let promise = new Promise<string>((resolve, reject) => {
          const materialidad = Object.assign({}, visita.materialidad, change)

          MaterialidadUtils.sendToServer(visita.id_visita, { field, value }, materialidad, { s: 1 }, (response) => {
            if (response && response.response.visita) {
              response.response.visita.materialidad = materialidad
              hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo_new, visita, response.response.visita)
              update(hoja_trabajo_new)
            }
            resolve(visita.id)
          })
        })

        promises.push(promise)
      })

      Promise.all(promises).then(function() {
        $.unblockUI()
      })
    })
  }

  /**
   * Handler al cambiar un valor numerico.
   *
   * @param field Campo modificado.
   * @param ev Evento.
   * @param value Valor ingresado.
   */
  onChangeNumericField(field: keyof ProyectoMaterialidad, ev: SyntheticEvent, value: number, persist: boolean = false) {
    let change: any = {}

    if (persist && (ev.target as any).value == '') {
      if (field == 'porc') {
        value = 75
      } else if (field == 'porc_error') {
        value = 5
      }
    }

    if (change[field] != value) {
      change[field] = value
      MaterialidadForm.changed.add(field)
    }

    this.setState(change, () => {
      if (persist && MaterialidadForm.changed.has(field)) {
        this.sendToServer({ field, value })
        MaterialidadForm.changed.delete(field)
      }
    })
  }

  calculateTotal(field: 'porc_general' | 'porc' | 'porc_error') {
    return MaterialidadUtils.calculateTotal(field, this.state, this.state.factor, this.state.nivel)
  }

  /**
   * Handler al cambiar el valor del monto de un factor.
   *
   * @param ev Evento
   * @param value Valor ingresado del monto.
   */
  onChangeMonto(ev: SyntheticEvent, value: number, useNivel: boolean, persist: boolean = false) {
    if (persist && (ev.target as any).value == '') {
      value = MaterialidadUtils.getDefaulPorcMG(this.state.factor, this.state.nivel)
    }

    let field: string

    this.setState(
      (state) => {
        let { montos, factor } = state
        if (typeof montos[factor] == 'undefined') {
          montos[factor] = { valor: 0 }
        }

        if (useNivel) {
          const { nivel } = state
          const nivel_field = MaterialidadUtils.getNivel(nivel)
          field = `monto${factor}_${nivel_field}`

          if (montos[factor][nivel_field] != value) {
            montos[factor][nivel_field] = value
            MaterialidadForm.changed.add(field)
          }
        } else {
          field = `monto${factor}`

          if (montos[factor].valor != value) {
            montos[factor].valor = value
            MaterialidadForm.changed.add(field)
          }

          if (persist && MaterialidadForm.changed.has(field)) {
            this.sendToServer({ field, value })
            MaterialidadForm.changed.delete(field)
          }
        }

        return { montos }
      },
      () => {
        if (persist && MaterialidadForm.changed.has(field)) {
          this.sendToServer({ field, value })
          MaterialidadForm.changed.delete(field)
        }
      }
    )
  }

  onChangeObservaciones = (observaciones: string, cb?: () => void) => {
    this.setState({ factor_justificacion: observaciones }, () => {
      this.sendToServer({ field: 'factor_justificacion', value: observaciones }, cb)
    })
  }

  onChangeAttachments = (attachments: ProyectoAttachment[]) => {
    this.setState({ Attachments: attachments })
    this.updateContext()
  }

  render() {
    let row2_disabled_text = null
    if (this.isFactorInvalid()) {
      row2_disabled_text =
        'Estos campos se encuentran deshabilitados debido a que es necesario que primero seleccione el factor para el clculo de la Materialidad General'
    } else if (!this.state.nivel || '' + this.state.nivel == '0') {
      row2_disabled_text =
        'Estos campos se encuentran deshabilitados debido a que es necesario que seleccione el nivel de confianza'
    }

    const calculateTotal = this.calculateTotal.bind(this)

    const row2 = (
      <Form.Group className={cx('form-group-2', row2_disabled_text ? 'ui-state-disabled' : null)}>
        <Form.Field
          width={5}
          className='ui column grid compact'
          error={
            !!this.state.factor && !!this.state.montos[this.state.factor] && !this.state.montos[this.state.factor].valor
          }
        >
          <div className='box-input six wide column'>
            <div className='box-input-inner'>
              <label htmlFor='materialidad_porc_general' title='% Materialidad General' className='noqtip'>
                %MG
              </label>
              <InputAutoNumeric
                value={this.getPorcMG()}
                onChange={(ev: SyntheticEvent, value: number) => this.onChangeMonto(ev, value, true, false)}
                onBlur={(ev: SyntheticEvent, value: number) => this.onChangeMonto(ev, value, true, true)}
                disabled={!this.state.factor}
                minimumValue={'0'}
                maximumValue={'100'}
                isEditable={this.props.isEditable}
                className='percent eight wide field'
                id='materialidad_porc_general'
              />
            </div>
          </div>
          <MaterialidadFieldTotal
            field='porc_general'
            name='Materialidad General'
            tooltip={`Monto de materialidad que resulta de la aplicacin de los porcentajes minimo, moderado o  mximo de los factores.`}
            calculateTotal={calculateTotal}
          />
        </Form.Field>
        <Form.Field width={6} className='ui column grid compact centered' error={!this.state.porc}>
          <div className='box-input five wide column'>
            <div className='box-input-inner'>
              <label htmlFor='materialidad_porc' title='% Materialidad de Planeacin' className='noqtip'>
                %MP
              </label>
              <InputAutoNumeric
                value={this.state.porc}
                onChange={(ev: SyntheticEvent, value: number) => this.onChangeNumericField('porc', ev, value, false)}
                onBlur={(ev: SyntheticEvent, value: number) => this.onChangeNumericField('porc', ev, value, true)}
                disabled={!this.state.factor}
                minimumValue={'0'}
                maximumValue={'100'}
                isEditable={this.props.isEditable}
                className='percent nine wide field'
                id='materialidad_porc'
              />
            </div>
          </div>
          <MaterialidadFieldTotal
            field='porc'
            name='Materialidad de Planeacin'
            tooltip={`Las partidas de los estados financieros que superen la cifra de materialidad de planeacin deben ser auditadas
            al ser consideradas las partidas de mayor riesgo de errores significativos.
            Determinar una adecuada materialidad de planeacin es importante para evitar sub auditar o sobre auditar.`}
            calculateTotal={calculateTotal}
          />
        </Form.Field>
        <Form.Field width={5} className='ui column grid compact' error={!this.state.porc}>
          <div className='box-input six wide column right floated'>
            <div className='box-input-inner'>
              <label htmlFor='materialidad_porc_error' title='% Error Tolerable' className='noqtip'>
                %ET
              </label>
              <InputAutoNumeric
                value={this.state.porc_error}
                onChange={(ev: SyntheticEvent, value: number) => this.onChangeNumericField('porc_error', ev, value)}
                onBlur={(ev: SyntheticEvent, value: number) => this.onChangeNumericField('porc_error', ev, value, true)}
                disabled={!this.state.factor}
                minimumValue={'0'}
                maximumValue={'100'}
                isEditable={this.props.isEditable}
                className='percent eight wide field'
                id='materialidad_porc_error'
              />
            </div>
          </div>
          <MaterialidadFieldTotal
            field='porc_error'
            name='Error Tolerable'
            tooltip={`Error mnimo considero significativo para cualquier partida de los estados financieros.
            Si las diferencias de auditoria superan el Error Tolerable se debe proponer un ajuste contable.`}
            calculateTotal={calculateTotal}
          />
        </Form.Field>
      </Form.Group>
    )

    let montoSeleccionado = ''
    if (this.state.montos[this.state.factor] && this.state.montos[this.state.factor].valor != null) {
      montoSeleccionado = +this.state.montos[this.state.factor].valor + ''
    }

    return (
      <Form id='materialidadform'>
        <Form.Group>
          <Form.Field width={5} error={!this.state.factor} className='fieldl'>
            <label htmlFor='materialidad_factor'>
              Factor para clculo MG <span className='required_field_mark'>*</span>
              <GenericTooltip
                title='Factor seleccionado'
                iconColor='olive'
                wide
                text={
                  <div className='content'>
                    Seleccione el factor financiero que a juicio profesional considere apropiado: <br />
                    a.) El cliente es una entidad orientada a utilidades, se aconseja el factor de resultado del
                    ejercicio, <br />
                    b.) El cliente es una entidad sin animo de lucro, se aconseja el factor de ingresos brutos, gastos
                    operacionales o activos netos, <br />
                    c.) El cliente es una entidad basada en activos, se aconseja el factor de activos netos, <br />
                    d.) Los resultados del ejercicio fluctuan significativamente de un ao a otro, se aconseja otros
                    factores como el promedio de los resultados de los ultimos 3 aos, <br />
                    e) Cualquier otro factor considerado por el auditor.
                  </div>
                }
              />
            </label>
            <Select
              id='materialiad_factor'
              options={this.getFactoresAsSelectChoices()}
              value={this.state.factor}
              disabled={!this.props.isEditable}
              selectOnNavigation={false}
              onChange={(_, { value }) => this.onSelectChoice('factor', value)}
            />
          </Form.Field>

          <Form.Field
            width={6}
            className={this.isFactorInvalid() ? 'ui-state-disabled' : ''}
            error={!!this.state.factor && !montoSeleccionado}
          >
            <label htmlFor='materialidad_monto'>
              Monto del Factor <span className='required_field_mark'>*</span>
            </label>
            {this.isFactorInvalid() ? (
              <Popup
                trigger={
                  <div className='input-wrapper'>
                    <input disabled className='ui input' />
                  </div>
                }
                header='Campo deshabilitado'
                content='Este campo se encuentra deshabilitado debido a que es necesario que primero seleccione el factor para el clculo de la Materialidad General'
                className='disable-text'
                on='hover'
                wide
                position='top center'
              />
            ) : (
              <InputAutoNumeric
                value={montoSeleccionado}
                onChange={(ev: SyntheticEvent, value: number) => this.onChangeMonto(ev, value, false, false)}
                onBlur={(ev: SyntheticEvent, value: number) => this.onChangeMonto(ev, value, false, true)}
                disabled={!this.state.factor}
                isEditable={this.props.isEditable}
                className='currency'
                id='materialiad_monto'
              />
            )}
          </Form.Field>

          <Form.Field width={5} error={!this.state.nivel} className='fieldr'>
            <label htmlFor='materialidad_nivel'>
              Nivel de Confianza <span className='required_field_mark'>*</span>
              <GenericTooltip
                title='Nivel de confianza'
                iconColor='olive'
                text={
                  <div>
                    Elija el nivel que considere apropiado:<br />
                    <ul style={{ marginLeft: 4, lineHeight: '1em' }} className='unstyled'>
                      <li style={{ marginBottom: 3, marginTop: 4 }}>
                        a.) <strong>Nivel bajo:</strong> para clientes de primer ao o auditorias recurrentes con
                        ajustes frecuentes de auditoria,
                      </li>
                      <li style={{ marginBottom: 3, marginTop: 4 }}>
                        b.) <strong>Nivel moderado:</strong> para auditorias recurrentes con pocos ajustes de auditoria,{' '}
                      </li>
                      <li style={{ marginBottom: 3, marginTop: 4 }}>
                        c.) <strong>Nivel alto:</strong> para auditorias recurrentes sin ajustes de auditoria.
                      </li>
                    </ul>
                  </div>
                }
              />
            </label>
            <Select
              id='materialidad_nivel'
              options={this.getNivelesAsSelectChoices()}
              value={this.state.nivel}
              disabled={!this.props.isEditable}
              selectOnNavigation={false}
              onChange={(_, { value }) => this.onSelectChoice('nivel', value)}
            />
          </Form.Field>
        </Form.Group>
        {row2_disabled_text ? (
          <Popup
            trigger={row2}
            content={row2_disabled_text}
            header='Campos deshabilitados'
            className='disable-text'
            on='hover'
            wide
            position='bottom center'
          />
        ) : (
          row2
        )}

        <Form.Group className={!this.state.factor_justificacion ? 'error' : ''}>
          <div className='ui raised segment' id='segment-materialidad-observaciones'>
            <Form.Field width={16} className='ui column hoverable-actions'>
              <label>
                Documente la Razn de la Seleccin del Factor y Nivel de Confianza{' '}
                <span className='required_field_mark'>*</span>
              </label>
              <MaterialidadObservacionesEditor
                data={this.state.factor_justificacion}
                attachments={this.state.Attachments}
                isEditable={this.props.isEditable}
                onChangeData={this.onChangeObservaciones}
                onChangeAttachments={this.onChangeAttachments}
              />
            </Form.Field>
          </div>
        </Form.Group>
      </Form>
    )
  }
}
