import { Component } from 'react'

import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab/Tab'

import MenuSidebarVisita from '../proyecto__hoja_trabajo/MenuSidebarVisita'
import { PasoObjetivoPane, PasoVisitaStatusActions } from '../proyecto__hoja_trabajo/AppVisitaTab'
import MaterialidadForm from './MaterialidadForm'

import MaterialidadPercentTable from './MaterialidadPercentTable'

type TabPaneVisitaProps = {
  visita: ProyectoPasoVisita
  active: boolean
  isEditable: boolean
}

export default class TabPaneVisita extends Component<TabPaneVisitaProps> {
  getCompletadoDisabledText() {
    let materialidad = this.props.visita.materialidad
    if (!materialidad.factor) {
      return 'El factor para el clculo de la Materialidad General an no ha sido seleccionado.'
    }

    if (!materialidad.nivel) {
      return 'El nivel de confianza an no ha sido seleccionado.'
    }

    if (
      typeof materialidad.montos[materialidad.factor] === 'undefined' ||
      !materialidad.montos[materialidad.factor].valor
    ) {
      return 'El monto del factor seleccionado aun no ha sido ingresado.'
    }

    const niveles_field: { 1: 'max'; 2: 'med'; 3: 'min' } = { 1: 'max', 2: 'med', 3: 'min' }
    if (!materialidad.montos[materialidad.factor][niveles_field[materialidad.nivel]]) {
      return 'El porcentaje de Materialidad General aun no ha sido ingresado.'
    }

    if (!materialidad.porc) {
      return 'El porcentaje de Materialidad de Planeacin aun no ha sido ingresado.'
    }

    if (!materialidad.porc_error) {
      return 'El porcentaje de Error Tolerable aun no ha sido ingresado.'
    }

    if (!materialidad.factor_justificacion) {
      return 'La razn de la seleccin del Factor y Nivel de Confianza aun no ha sido ingresada.'
    }

    if (this.props.visita.additionals.revisiones > 0) {
      return 'Esta visita tiene Notas de Revisin pendientes por revisar que deben primero ser revisadas para poder marcar esta hoja de trabajo como COMPLETADA.'
    }
  }

  render() {
    let materialidad = this.props.visita.materialidad
    materialidad.Visita = this.props.visita

    return (
      <Tab.Pane active={this.props.active}>
        <MenuSidebarVisita visita={this.props.visita} actionItems={{ _ajustes: false, _muestreo: false }} />
        <div id='materialidadformwrapper'>
          <PasoObjetivoPane isEditable={this.props.isEditable} />

          <MaterialidadForm materialidad={materialidad} isEditable={this.props.isEditable} />

          <div className='footer-actions'>
            <MaterialidadPercentTable materialidad={materialidad} isEditable={this.props.isEditable} />

            <PasoVisitaStatusActions
              visita={this.props.visita}
              completadoDisabledText={this.getCompletadoDisabledText()}
            />
          </div>
        </div>
      </Tab.Pane>
    )
  }
}
